import {
  ComponentProps,
  withStreamlitConnection,
  StreamlitComponentBase,
  Streamlit,
} from 'streamlit-component-lib'
import React, { 
  ReactNode } from 'react'

import styles from './style.module.scss';

interface Options {
  layer: string
  units: string
}

interface State {
  action: string
  data: string
  label: string
  defaultChecked: boolean
  uniqueId: string
  options: Options
  isFocused: boolean
  isChecked: boolean
}

declare global {
  interface Window {
    chrome: any;
  }
}

class SendGeometry extends StreamlitComponentBase<State> {

  public constructor(props: ComponentProps) {
    super(props)

    // Values from user input
    const data = this.props.args['data'] as string
    const uniqueId = this.props.args['uniqueId'] as string
    const defaultChecked = this.props.args['defaultChecked'] as boolean
    const options = this.props.args['options'] as Options
    const label = this.props.args['label'] as string

    const initialState = {
      action: '',
      data: data,
      uniqueId: uniqueId,
      defaultChecked: defaultChecked,
      options: options,
      label: label,
      isChecked: defaultChecked ? true : false
    } as State

    this.state = {
      ...initialState
    }

    // Send message to DotNet
    if (defaultChecked)
    {
      const initialAction = 'DrawGeometry';
      let msg = this._sendMessageTo(initialAction);
      // Update only the action
      this.setState({action: initialAction});
    }
  }

  // // Live refresh
  // componentDidMount() {
  //   console.log('[POLLINATION-DEBUG]: mount.');
  //   Streamlit.setFrameHeight();
  // }

  componentWillUnmount()
  {
    if (this.state.isChecked)
    {
      // Disable draw
      let msg = this._sendMessageTo('CombinedRendering');
      console.log('[POLLINATION-DEBUG]: unmount.');
    }
  }

  componentDidUpdate() {
    if (this.state.isChecked && 
      this.state.data != this.props.args['data'])
    {
      // Disable draw firstly
      let msg = this._sendMessageTo('CombinedRendering');
      // Save data inside the state
      this._setStateNow(msg, true);
      console.log('[POLLINATION-DEBUG]: auto-update.');
    }
  }

  public render = (): ReactNode => {
    return (
      <label className={styles.poCheckboxLabel}>
        <input 
          className={styles.poCheckbox}
          type='checkbox' 
          defaultChecked={this.state.defaultChecked} 
          onChange={this._onChange} 
          disabled={this.props.disabled}
          onFocus={this._onFocus}
          onBlur={this._onBlur}/>
        <span className={styles.poCheckboxSpan}>
          {this.state.label}
        </span>
      </label>
    )
  }

  private _setStateNow = (msgObject: State, 
    status: boolean): void =>{
    this.setState(
      (prevState) => ({ 
        ...prevState, 
        action: msgObject.action,
        data: msgObject.data,
        uniqueId: msgObject.uniqueId,
        options: msgObject.options,
        isChecked: status
      })
    );
    console.log('[POLLINATION-DEBUG]: JSON string sent.');
  }

  private _sendMessageTo = (command: string): State => {
    if (typeof window.parent.chrome != 'undefined') {
      if (typeof window.parent.chrome.webview == 'undefined')
      {
        console.log('[POLLINATION-DEBUG]: webview not found.');
        return this.state;
      }
      
      var msgObject = {
        action: command,
        data: this.props.args['data'],
        uniqueId: this.props.args['uniqueId'],
        options: this.props.args['options']
      } as State;

      var json = JSON.stringify(msgObject);
      window.parent.chrome.webview.postMessage(json);
      return msgObject;
    }
    else
    {
      console.log('[POLLINATION-DEBUG]: chrome not found.');
      return this.state;
    }
  }

  private _onChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    let check = event.target.checked;
    let dotNetAction = (check) ? 'DrawGeometry' : 'DisableDraw';
    let msg = this._sendMessageTo(dotNetAction);
    this._setStateNow(msg, check);
  }

  private _onFocus = (): void => {
    this.setState({ isFocused: true })
  }

  private _onBlur = (): void => {
    this.setState({ isFocused: false })
  }
}

export default withStreamlitConnection(SendGeometry)
